import React,{Fragment,PureComponent} from "react";
import {connect} from 'react-redux';
import {actionCreators} from "../../store";
import {Common} from "../../../Cesium/method";
import axios from "axios";
import $ from 'jquery';

class ConfigData1 extends PureComponent{
    constructor(props){
        super(props);
        ConfigData1.this = this;
        this.state = {
            navItemShow:false,
            Nav:true,//
            navItem:0, //id,id1id,2
            X:'',//x
            Y:'',//y
            Z:'',//z
            labelname:'',//
            labelid:'',//id
            Floor : JSON.parse(sessionStorage.getItem('Floor')),//
            CameraView : '',//
            CameraViewState : false,
            ShowPublic:false,
            OneHtml:true,
            TwoHtml:false,
        }
        this.CameraView = this.CameraView.bind(this);//
        this.AssignedLabelID = this.AssignedLabelID.bind(this);//
        this.NavClick = this.NavClick.bind(this);//
        this.SaveNav = this.SaveNav.bind(this);//
    }
    componentDidMount(){
        console.log(ConfigData1.this.Floor);
        window.receiveMessageFromIndex=function(e){
            if(e!==undefined){
                console.log('react,iframeID',e.data);
                switch(e.data.switchName){
                    case'GeoaddlabelV2':
                        sessionStorage.setItem('labelData',this.JSON.stringify(e.data));
                        var datas =  JSON.parse(sessionStorage.getItem('labelData')).style.position;
                        ConfigData1.this.setState({
                            X:datas.x,
                            Y:datas.y,
                            Z:datas.z
                        })
                        break;
                    case 'getCameraView':
                        ConfigData1.this.setState({
                            CameraView : this.JSON.stringify(e.data.cv)
                        })
                        console.log('',ConfigData1.this.state.CameraView);
                        break;
                    default:
                        return null;
                }

            }
        }
        //message
        window.addEventListener("message",window.receiveMessageFromIndex,false);

        this.initData();//
    };
    // 
    initData(){
        var Floors=ConfigData1.this.state.Floor;
        if(Floors[0].FloorItems.center_position == null){
            this.setState({
                labelname:Floors[0].FloorItems.build_name,//
                labelid:Floors[0].FloorItems.id,//id
            })
        }else{
            var list=JSON.stringify({x:Floors[0].FloorItems.center_position.x,y:Floors[0].FloorItems.center_position.y,z:Floors[0].FloorItems.center_position.z,h:Floors[0].FloorItems.center_position.heading,p:Floors[0].FloorItems.center_position.pitch,r:Floors[0].FloorItems.center_position.roll});
            console.log(list);
            this.setState({
                X:Floors[0].FloorItems.label_position.x,//x
                Y:Floors[0].FloorItems.label_position.y,//y
                Z:Floors[0].FloorItems.label_position.z,//z
                labelname:Floors[0].FloorItems.build_name,//
                labelid:Floors[0].FloorItems.id,//id
                CameraView : JSON.stringify({x:Floors[0].FloorItems.center_position.x,y:Floors[0].FloorItems.center_position.y,z:Floors[0].FloorItems.center_position.z,h:Floors[0].FloorItems.center_position.heading,p:Floors[0].FloorItems.center_position.pitch,r:Floors[0].FloorItems.center_position.roll}),//
            })
        }

    }
    //
    GeoaddlabelV2(){
        console.log(this.state.labelid)
        Common.GeoaddlabelV2(this.state.labelname,this.state.labelid);//labelname
    }
    //id
    AssignedLabelID(e,item){
        console.log(item);
        var Position=item.FloorItems.center_position;
        if(Position === null){
            console.log(item.FloorItems.id)
            this.setState({
                labelid:item.FloorItems.id,
                labelname:item.FloorItems.build_name,
                X:'',//x
                Y:'',//y
                Z:'',//z
                CameraView : '',//
                OneHtml:true,
                TwoHtml:false,
            })
        }else{
            this.setState({
                labelid:item.FloorItems.id,
                labelname:item.FloorItems.build_name,
                X:item.FloorItems.label_position.x,//x
                Y:item.FloorItems.label_position.y,//y
                Z:item.FloorItems.label_position.z,//z
                CameraView : JSON.stringify({x:Position.x,y:Position.y,z:Position.z,h:Position.heading,p:Position.pitch,r:Position.roll}),//
                OneHtml:true,
                TwoHtml:false,
            })
        }
        e.stopPropagation();
        // <h1>
        let node = $(e.target);

        // <h1>
        let subMenu = node.children();

        // /
        subMenu.css("display", subMenu.css('display') === "none" ? "block" : "none");

    }
    /**
     * 
     */
    RefreshLabel(){
        var that = this;
        setTimeout(function(){
            Common.ModifyLabel({id:that.state.labelid,labeltxt : that.state.labelname,x:Number(that.state.X),y:Number(that.state.Y),z:Number(that.state.Z)});
        },0)
    }
    //
    Push(){
        var Floors = [];
        axios.get(global.Url+'map/build/list',{}).then((res) => {
            const result = res.data.data;
            if(result) {
                for (let index = 0; index < result.length; index++) {
                    axios.post(global.Url+'map/floor/list',{build_id:result[index].id}).then((res) => {
                        const results = res.data.data;
                        if(results) {
                            Floors.push(
                                {
                                    FloorItems:result[index],
                                    Floorlists:results
                                }
                            )
                            if(index === result.length - 1)
                            {
                                var FloorsList=JSON.stringify(Floors);
                                console.log(FloorsList);
                                sessionStorage.setItem('Floor',FloorsList);
                                ConfigData1.this.setState({
                                    Floor:Floors
                                })
                            }
                        }else{
                            alert(res.data.msg)
                        }
                    })
                }
            }else{
                alert(res.data.msg)
            }

        })
    }
    //
    SubmitBuildingInformation(){
        console.log(this.state.CameraView)
        if(!this.state.CameraView){
            //alert("")
            $(".PageShow").show().find('h1').html("");
            global.Time();
        }else if(this.state.X === '' || this.state.labelid === ''){
            //alert("")
            $(".PageShow").show().find('h1').html("");
            global.Time();
        }else{
            var  CameraView =  JSON.parse(this.state.CameraView);
            var data =
                {
                    id: this.state.labelid,
                    build_name: this.state.labelname,
                    center_position: {
                        y: CameraView.y,
                        x: CameraView.x,
                        z: CameraView.z,
                        heading: CameraView.h,
                        pitch: CameraView.p,
                        roll: CameraView.r
                    },
                    label_position: {
                        x:Number(this.state.X),y:Number(this.state.Y),z:Number(this.state.Z),
                        heading: 0,
                        itch: 0,
                        roll: 0
                    },
                    remark: 1
                }
            axios.post(global.Url+'map/build/update',data).then((res) => {
                const result = res.data.data;
                if(result) {
                    $(".PageShow").show().find('h1').html('');
                    global.Time();
                    //alert("");
                    this.Push();
                }else{
                    alert(res.data.msg)
                }
            })
            console.log(data)
        }


    }
    //X
    changeX(event){
        this.setState({
            X: event.target.value
        });
        this.RefreshLabel();
        console.log(event.target.value)
    }
    //Y
    changeY(event){
        this.setState({
            Y: event.target.value
        });
        this.RefreshLabel();
        console.log(event.target.value)
    }
    //Z
    changeZ(event){
        this.setState({
            Z: event.target.value
        });
        this.RefreshLabel();
        console.log(event.target.value)
    }
    //
    Modifylabelname(event){
        this.setState({
            labelname: event.target.value
        });
        this.RefreshLabel();
        console.log(event.target.value)
    }
    //
    CameraView(){
        var Dates= JSON.parse(ConfigData1.this.state.CameraView);
        if(Dates===null){
            //alert("");
            $(".PageShow").show().find('h1').html("");
            global.Time();
        }else{
            console.log(Dates);
            var cv = {
                x:Dates.x,
                y:Dates.y,
                z:Dates.z,
                h: Dates.h,
                p: Dates.p,
                r: Dates.r,
            };
            var frame=document.getElementById("core_content");
            var sendData={
                Event : '',
                ModName : "RetrospectiveCameraView",
                parameter :cv
            };
            frame.contentWindow.postMessage(sendData,"*");
        }

    }
    //
    NavClick(e,name){
        e.stopPropagation();
        var centerPosition=name.center_position;
        console.log(name);
        if(centerPosition===null){
            this.setState({
                labelname:name.floor_name,
                CameraView : JSON.stringify(centerPosition),//
                OneHtml:false,
                TwoHtml:true
            })
        }else{
            this.setState({
                labelname:name.floor_name,
                CameraView : JSON.stringify({x:centerPosition.x,y:centerPosition.y,z:centerPosition.z,h:centerPosition.heading,p:centerPosition.pitch,r:centerPosition.roll}),//
                OneHtml:false,
                TwoHtml:true
            })
        }
        sessionStorage.setItem('navId',name.id);
    }
    //
    SaveNav(){
        var Id=sessionStorage.getItem('navId');
        var center=JSON.parse(ConfigData1.this.state.CameraView);
        if(center===null){
            //alert("!");
            $(".PageShow").show().find('h1').html("");
            global.Time();
        }else{
            var data={
                "id": Id,
                "floor_name": ConfigData1.this.state.labelname,
                "center_position": {
                    "y": center.y,
                    "x": center.x,
                    "z": center.z,
                    "heading": center.h,
                    "pitch": center.p,
                    "roll": center.r
                },
                "is_underground": true,
                "rise_height": 10,
                "remark": "1F"
            }
            axios.post(global.Url+'map/floor/update',data).then((res) => {
                const results = res.data.data;
                if(results) {
                    //alert("");
                    $(".PageShow").show().find('h1').html('');
                    global.Time();
                    this.Push();
                }else{
                    alert(res.data.msg)
                }
            })
        }

    }
    render(){
        const {Floor,X,Y,Z,labelname,CameraView,OneHtml,TwoHtml} = this.state;
        const {navClick} = this.props;
        return(
            <Fragment>
                <div className="configShow" style={{'width':'653px','height': '590px'}}>
                    <div className="configShowNav"> <i onClick={() => navClick(100)}></i></div>
                    <div className="NavigationButtonBorder">
                        <div className="configBut" style={{'marginTop': '0px'}}><button style={{'position':'absolute','marginLeft':'20px'}}></button><button style={{'marginRight': '380px'}}></button></div>
                    </div>
                    <div className="DataConfigTreeDiv">
                        <ul>
                            {
                                Floor.length > 0 && Floor.map(
                                    (item,index) => {
                                        return  <li key={index} onClick={(e)=>this.AssignedLabelID(e,item)}>{item.FloorItems.build_name}
                                            <ul className='navItemList'>
                                                {
                                                    item.Floorlists.length > 0 && item.Floorlists.map(
                                                        (name,i) => {
                                                            return  <li key={i} onClick={(e)=>this.NavClick(e,name)}>{name.floor_name}</li>
                                                        }
                                                    )
                                                }
                                            </ul>
                                        </li>
                                    }
                                )
                            }
                        </ul>
                    </div>
                    {OneHtml
                        ?
                        <div className="DataOne">
                            <h1></h1>
                            <li><span></span><input type="text" value={labelname} onChange={(event) => this.Modifylabelname(event)}/></li>
                            <li><span></span><button onClick={()=>this.GeoaddlabelV2()}></button></li>
                            <li>
                                <span></span>
                                <div className="DataRight">
                                    <span><label>X</label><input type="text" defaultValue={X} onChange={(event) => this.changeX(event)}/></span>
                                    <span><label>Y</label><input type="text" defaultValue={Y} onChange={(event) => this.changeY(event)}/></span>
                                    <span><label>Z</label><input type="text" defaultValue={Z} onChange={(event) => this.changeZ(event)}/></span>
                                </div>
                            </li>
                            <li><span></span>
                                <span className='newNav'>
                                <li  onClick={() => this.CameraView()}></li><li className='next' onClick={() => Common.onCoordinate()}></li>
                                <div className="liCont">{CameraView}</div>
                            </span>
                            </li>
                            <div className="configBut" onClick={() => this.SubmitBuildingInformation()}><button></button></div>
                        </div>
                        :''
                    }
                    {TwoHtml
                        ?
                        <div className="DataOne">
                            <h1></h1>
                            <li><span></span><input type="text" defaultValue='' value={labelname} onChange={(event) => this.Modifylabelname(event)}/></li>
                            <li><span></span>
                                <span className='newNav'>
                                <li  onClick={() => this.CameraView()}></li><li className='next' onClick={() => Common.onCoordinate()}></li>
                                <div className="liCont">{CameraView}</div>
                            </span>
                            </li>
                            <div className="configBut" onClick={() => this.SaveNav()}><button></button></div>
                        </div>
                        :''
                    }
                </div>
            </Fragment>
        )
    }
}

const mapDispatch = (dispatch) => ({
    navClick(id,item) {
        dispatch(actionCreators.nav_click(id,item));
    },
});

export default connect(null,mapDispatch)(ConfigData1);