import React,{PureComponent} from "react";
import '../../style/common.css';
import Video from '../../images/video-diqiu.mp4';
import {Redirect} from 'react-router-dom';
import DocumentTitle from 'react-document-title';
import LoGO from '../../images/loginLogo.png';
import axios from 'axios';


class Index extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            typeID:0,  //01
            loginState:false, //
            user:'', //
            password:'', //
            Show:false  //
        }
        this.UserClick = this.UserClick.bind(this);   //
        this.userChange = this.userChange.bind(this);  //
        this.passwordChange = this.passwordChange.bind(this); //
        this.login = this.login.bind(this);  //
    };
    //
    userChange = (event) => {
        //console.log(event.target.value);
        this.setState({
            user: event.target.value
        });
    };
    //
    passwordChange = (event) => {
        this.setState({
            password: event.target.value
        });
    };
    render(){
        const {loginState,typeID,user,password,Show} = this.state;
        if(!loginState){
            return(
                <DocumentTitle title=''>
                    <div id="loginBJ" className="loginContainer">
                        <video muted="muted" autoPlay="autoplay" loop="loop" className="video">
                            <source type="video/mp4" src={Video} />
                        </video>
                        <div className="loginLogo"><img src={LoGO} alt=""/><br/></div>
                        <div className="login">
                            <div className="loginWrapper">
                                {
                                    Show ?
                                        <h1>
                                            <span className="cur1" onClick={() => this.UserClick(1)}></span>
                                        </h1>
                                        :
                                        <h1>
                                            <span className={typeID === 0 ? "cur" :" "} onClick={() => this.UserClick(0)}></span>
                                            <span className={typeID === 1 ? "cur" :" "}  onClick={() => this.UserClick(1)}></span>
                                        </h1>
                                }

                                <input type="text" value={user} onChange={(event) => this.userChange(event)} placeholder=""/>
                                <input type="password" value={password} onChange={(event) => this.passwordChange(event)} placeholder=""/>
                                <button onClick={() => this.login(user,password,typeID)}><i></i></button>
                            </div>
                        </div>
                    </div>
                </DocumentTitle>
            )
        }else{
            if(typeID===1){
                return <Redirect to="/admin" />
            }else{
                return <Redirect to="/" />
            }
        }

    }
    //
    UserClick(id) {
        //console.log(id);
        this.setState({
            typeID: id,
        });
    }
    //
    login(user,password,id){
        if(this.state.Show){
            id=1
        }
        if(!user){
            alert("");
        }else if(!password){
            alert("")
        }else{
            axios.post(global.Url+'sys/user/login',{
                user_name:user,
                user_pwd:password,
                database:global.Database
            }).then((res) => {
                const result = res.data.data;
                if(result) {
                    sessionStorage.setItem('role',result.role_id);
                    sessionStorage.setItem('userName',result.full_name);
                    sessionStorage.setItem('user',result.user_name);
                    sessionStorage.setItem('typeId',id);
                    //console.log(result);
                    //console.log(result.full_name);
                    if(id === 1){
                        this.props.history.push( '/admin')
                    }else if(id === 0){
                        this.props.history.push( '/')
                    }

                }else{
                    alert('')
                }
            })
        }
    }
    //
    componentDidMount() {
        document.body.addEventListener("keyup",(e) => {
            if(window.event){
                e = window.event
            }
            let code = e.charCode || e.keyCode
            if(code === 13){
                this.login(this.state.user,this.state.password,this.state.typeID)
            }
        })
    }
    componentWillMount() {  //omponentDidMount
        document.body.removeEventListener('keyup',() => {})
        axios.get(global.Url+'sys/config/isLogin').then((res) => {
            const result = res.data.data;
            if(result) {
                //console.log(result);
                //sessionStorage.setItem('isLogin',result.is_login);
                this.setState({
                    Show: result.is_login //  truefalse
                });

            }else{
                alert(res.msg)
            }
        })

    }
}

export default Index;