import React,{PureComponent,Fragment} from 'react';
import '../../../style/common.css';
import {actionCreators} from "../store";
import {connect} from "react-redux";
import {Common} from "../../Cesium/method";
import $ from "jquery";


let RelationalLock = {
    RainwaterSystem : true, //
    snowSystem : true, //
    Fogging : true, //
    enableLighting : true, //
    MeasurementHeight: true,//
    measureLineSpace:true,//
    measureAreaSpace:true,//
    onSurveillance:true,//

};

class More extends PureComponent{
    constructor(props){
        super(props);
        this.state={
            activeBtn:false
        }
        More.this = this;
        this.changeStatus = this.changeStatus.bind(this)
    }
    changeStatus(){
        this.setState({
            activeBtn:!this.state.activeBtn
        })
    }
    //
    MeasurementHeight(){
        if(RelationalLock.MeasurementHeight){
            RelationalLock.MeasurementHeight = false;
            Common.MeasurementHeight();
        }else{
            RelationalLock.MeasurementHeight = true;
            Common.RemoveHeight();
        }
    }
    //
    measureLineSpace(){
        if(RelationalLock.measureLineSpace){
            RelationalLock.measureLineSpace = false;
            Common.measureLineSpace();
        }else{
            RelationalLock.measureLineSpace = true;
            Common.RemoveLine();
        }
    }
    //
    measureAreaSpace(){
        if(RelationalLock.measureAreaSpace){
            RelationalLock.measureAreaSpace = false;
            Common.measureAreaSpace();
        }else{
            RelationalLock.measureAreaSpace = true;
            Common.RemoveArea();
        }
    }
    //
    snowSystem(){
        if(RelationalLock.snowSystem){
            RelationalLock.snowSystem = false;
            Common.snowSystemV2();
        }else{
            RelationalLock.snowSystem = true;
            Common.snowDelete();
        }
    }
    //
    RainwaterSystem(){
        if(RelationalLock.RainwaterSystem){
            RelationalLock.RainwaterSystem = false;
            Common.RainwaterSystemV2();
        }else{
            RelationalLock.RainwaterSystem = true;
            Common.RainDelete();
        }
        //alert('a');
    }
    //
    Fogging(){
        if(RelationalLock.Fogging){
            RelationalLock.Fogging = false;
            Common.Fogging();
        }else{
            RelationalLock.Fogging = true;
            Common.fogDelete();
        }
    }
    //
    enableLighting(){
        if(RelationalLock.enableLighting){
            RelationalLock.enableLighting = false;
            Common.enableLightingV2();
        }else{
            RelationalLock.enableLighting = true;
            Common.lightDelete();
        }
    }
    //
   Surveillance(){
        if(RelationalLock.onSurveillance){
            RelationalLock.onSurveillance = false;
            Common.onSurveillance();
        }else{
            RelationalLock.onSurveillance = true;
            Common.onMap();
        }
    }
    render(){
        const {index,clickIcon,moreIndex,clickIconItem,clickIconItemNav} = this.props;
        const {activeBtn} = this.state;
        return(
            <Fragment>
                <li onClick={() => {clickIcon(3);this.changeStatus()}} className={activeBtn && index === 3  ? 'active' :''}>
                    <i className="icon3"></i><span></span>
                </li>
                {index === 3 && activeBtn ?
                    <div className="MoreShow">
                        <div className="MoreItem" id="MoreItem1" onClick ={(even) => clickIconItem(even,1)} ><i className="more1"></i></div>
                        <div className="MoreItem" id="MoreItem2" onClick ={(even) => clickIconItem(even,2)} ><i className="more2"></i></div>
                        <div className="MoreItem" id="MoreItem3" onClick ={(even) => clickIconItem(even,3)}  >
                            <i className="more3"></i>
                            {
                                moreIndex === 3 ?
                                    <div className="LcShow">
                                        <div className="LcItem" id="LcItem1" onClick ={(even) => clickIconItemNav(even,1)}></div>
                                        <div className="LcItem" id="LcItem2" onClick ={(even) => clickIconItemNav(even,2)}></div>
                                        <div className="LcItem" id="LcItem3" onClick ={(even) => clickIconItemNav(even,3)}></div>
                                    </div>
                                    : " "
                            }
                        </div>
                        <div className="MoreItem" id="MoreItem4" onClick ={(even) => clickIconItem(even,4)}>
                            <i className="more4"></i>
                            {
                                moreIndex === 4 ?
                                    <div className="LcShow">
                                        <div className="LcItem" id="LcItem4" onClick ={(even) => clickIconItemNav(even,4)}><span></span> <i className="LcIcon1"></i></div>
                                        <div className="LcItem" id="LcItem5" onClick ={(even) => clickIconItemNav(even,5)}><span></span> <i className="LcIcon2"></i></div>
                                        <div className="LcItem" id="LcItem6" onClick ={(even) => clickIconItemNav(even,6)}><span></span> <i className="LcIcon3"></i></div>
                                        <div className="LcItem" id="LcItem7" onClick ={(even) => clickIconItemNav(even,7)}><span></span> <i className="LcIcon4"></i></div>
                                    </div>
                                    : " "
                            }
                        </div>
                        <div className="MoreItem" id="MoreItem5" onClick ={(even) => clickIconItem(even,5)}><i className="more5"></i></div>
                        <div className="MoreItem" id="MoreItem6" onClick ={(even) => clickIconItem(even,6)}><i className="more6"></i></div>
                        <div className="MoreItem" id="MoreItem7" onClick ={(even) => clickIconItem(even,7)}><i className="more7"></i></div>
                    </div>
                    : ''}
            </Fragment>
        )
    }
}

const mapState = (state) => {
    return{
        index:state.footer.get('index'),
        moreIndex:state.footer.get('moreIndex'),
        moreIndexItem:state.footer.get('moreIndexItem'),
    }
};

const mapDispatch = (dispatch) => ({
    clickIcon(item) {
        dispatch(actionCreators.click_Icon(item));
    },
    clickIconItem(e,item) {
        e.stopPropagation(); // e.preventDefault();
        dispatch(actionCreators.click_Icon_Item(item));
        $(".MoreShow").children(".MoreItem").removeClass('active');
        $("#MoreItem"+item).addClass('active');
        switch (item) {
            case 1:
                alert('');
                break;
            case 2:
                //alert('');
                Common.FlightRoaming();
                break;
            case 5:
                //alert('');
                Common.FlightRoaming();
                break;
            case 6:
                //alert('');
                More.this.Surveillance();
                break;
            case 7:
                alert('');
                break;
            default:
                return null;
        }
    },
    clickIconItemNav(even,item) {
        even.stopPropagation(); //
        dispatch(actionCreators.click_Icon_Item_Nav(item));
        $("#LcItem"+item).toggleClass('active');
        switch (item) {
            case 1:
                //alert('');
                More.this.MeasurementHeight();
                break;
            case 2:
                //alert('');
                More.this.measureLineSpace();
                break;
            case 3:
                //alert('');
                More.this.measureAreaSpace();
                break;
            case 4:
                //
                //alert('');
                More.this.RainwaterSystem();
                break;
            case 5:
                //
                //alert('');
                More.this.snowSystem();
                break;
            case 6:
                //alert('');
                More.this.Fogging();
                // Weather.C();
                break;
            case 7:
                //
                //alert('');
                More.this.enableLighting();
                break;
            default:
                return null;
        }
    }
});
export default connect(mapState,mapDispatch)(More);
