import * as actionTypes from './actionTypes';
import { fromJS } from "immutable";

const defaultState = fromJS({
    adminItem:0, //
    adminIndex:0, // 1,2
    adminIndexItem:0,
});
export default (state = defaultState, action) => {
    switch(action.type){
        case actionTypes.CLICK_NAV:
            return state.merge({
                adminItem:action.id,
                adminIndex:action.item,
                adminIndexItem:0
            });
        default:
            return state;
    }
}