import React,{Fragment,PureComponent} from "react";
import {connect} from 'react-redux';
import {actionCreators} from "../../store";
import axios from "axios";
import {Common} from "../../../Cesium/method";
import $ from "jquery";

class ConfigData2 extends PureComponent{
    constructor(props){
        super(props);
        this.state={
            isLoad:true,//
            mapList:{},//
            mapName:'',//
            mapTagName:'',//
            fontSize:'1',//
            fontColor:'#ff0000',//
            borderWitch:'1',//
            borderColor:'#ffffff',//
            centerPosition:{},//
            labelPosition:{},//
            labelStyle:{},//
            labelId:'',//Id
            oneMap:true,//
            twoMap:false,//
            deletedId:''//ID
        }
        ConfigData2.this = this;
        this.Close = this.Close.bind(this);//
        this.setTag = this.setTag.bind(this);//
        this.saveTag = this.saveTag.bind(this);//
        this.AddList = this.AddList.bind(this);//
        this.changeTag = this.changeTag.bind(this);//
        this.ChangeList = this.ChangeList.bind(this);//
        this.onContextMenu = this.onContextMenu.bind(this);//
        this.DeletedTag = this.DeletedTag.bind(this);//
        this.PageDeletedData2 =this.PageDeletedData2.bind(this);
    }
    componentDidMount() {
        this.onLoad();//
        //
        document.oncontextmenu = function(){
            return false;
        }
        window.receiveMessageFromIndex=function(e){
            if(e!==undefined){
                switch(e.data.switchName){
                    case'GeoaddlabelV3':
                        console.log(e.data);
                        var Parameter=e.data.parameter
                        ConfigData2.this.setState({
                            labelId:Parameter.label_id,
                            labelStyle:Parameter.label_style,
                            labelPosition:Parameter.label_position,
                            mapName:Parameter.label_name,
                        })
                        break;
                    case'getCameraView':
                        var cv=e.data.cv;
                        ConfigData2.this.setState({
                            centerPosition:{
                                y: cv.y,
                                x: cv.x,
                                z: cv.z,
                                heading: cv.h,
                                pitch: cv.p,
                                roll: cv.r
                            }
                        })
                        break;
                    default:
                        return null;
                }

            }
        }
        //message
        window.addEventListener("message",window.receiveMessageFromIndex,false);
    }
    //
    onLoad(){
        axios.get(global.Url+'map/label/list').then((res)=>{
            constresults=res.data.data;
            if(results){
                this.setState({
                    mapList:results
                })
            }else{
                alert(res.msg)
            }
        })
    }
    //
    Close(){
        this.setState({
            isLoad:false
        })
    }
    //
    changeName(event){
        this.setState({
            mapName: event.target.value
        });
        ConfigData2.this.onChangeData()
    }
    //
    changFont(event){
        this.setState({
            fontSize: event.target.value
        });
        ConfigData2.this.onChangeData()
    }
    //
    ChangeColor(e){
        ConfigData2.this.setState({
            fontColor:e.target.value
        })
        ConfigData2.this.onChangeData()
    }
    //
    changeBorder(event){
        this.setState({
            borderWitch: event.target.value
        });
        ConfigData2.this.onChangeData()
    }
    //
    ChangeBorderColor(e){
        ConfigData2.this.setState({
            borderColor:e.target.value
        })
        ConfigData2.this.onChangeData()
    }
    //
    setTag(){
        const {labelId,mapName,fontSize,fontColor,borderWitch,borderColor} = this.state;
        if(!mapName){
            //alert("");
            $(".PageShow").show().find('h1').html("!");
            global.Time();
        }else{
            var data={
                id:labelId,
                label_name:mapName,
                label_style:{
                    fontSize:fontSize,
                    fontColor:fontColor,
                    borderWitch:borderWitch,
                    borderColor:borderColor
                }
            };
            console.log(data);
            Common.GeoaddlabelV3(data);
        }
    }
    //
    saveTag(){
        var list=ConfigData2.this.state;
        if(!list.mapName){
            //alert("");
            $(".PageShow").show().find('h1').html("!");
            global.Time();
        }else if(JSON.stringify(list.labelPosition) === "{}"){
            //alert("");
            $(".PageShow").show().find('h1').html("!");
            setTimeout(function () {
                $(".PageShow").hide();
            },1000)
        }else{
            Common.onCoordinate();
            setTimeout(function () {
                var list=ConfigData2.this.state;
                var data={
                    label_name: list.mapName,
                    center_position: list.centerPosition,
                    label_position: list.labelPosition,
                    label_style: list.labelStyle,
                    remark: "1"
                }
                console.log("data",list.centerPosition);
                if(JSON.stringify(list.centerPosition) === "{}"){
                    alert("Cesium");
                }else{
                    axios.post(global.Url+'map/label/add',data).then((res)=>{
                        constresults=res.data.data;
                        if(results &&res.data.msg==='success'){
                            console.log(res,results);
                            $(".PageShow").show().find('h1').html("!");
                            global.Time();
                            Common.ResetLabel();
                            ConfigData2.this.onLoad();
                            ConfigData2.this.setState({
                                oneMap:false,
                            })
                            ConfigData2.this.setEmty();

                        }else{
                            alert(res.data.msg)
                        }
                    })
                }


            },10)
        }
    }
    //
    setEmty(){
        ConfigData2.this.setState({
            oneMap:true,
            mapName:'',
            centerPosition:{},
            labelPosition:{},
            labelStyle:{},
            fontSize:'1',//
            fontColor:'#ff0000',//
            borderWitch:'1',//
            borderColor:'#ffffff',//
            labelId:'',//Id
        })
    }
    //
    AddList(){
        this.setState({
            oneMap:true,
            twoMap:false,
        })
        ConfigData2.this.setEmty();
        $(".RightDelete").css({'display':'none'})
    }
    //
    ChangeList(id){
        this.setState({
            oneMap:false,
            twoMap:true,
        })
        $("#Right"+id).css({'color':'#54b2df','fontWeight':'bold'});
        $("#Right"+id).siblings().css({'color':'#fff','fontWeight':'100'});
        $("#Right"+id).find('.RightDelete').css({'display':'none'});
        $("#Right"+id).siblings().find('.RightDelete').css({'display':'none'});
        axios.post(global.Url+'map/label',{id:id}).then((res)=>{
            constresults=res.data.data;
            if(results){
                console.log(results,results.label_style.fontSize);
                ConfigData2.this.setState({
                    mapName:results.label_name,//
                    mapTagName:results.label_name,//
                    fontSize:results.label_style.fontSize,//
                    fontColor:results.label_style.fontColor,//
                    borderWitch:results.label_style.borderWitch,//
                    borderColor:results.label_style.borderColor,//
                    centerPosition:results.center_position,//
                    labelPosition:results.label_position,//
                    labelId:results.id,//Id
                    labelStyle:{
                        borderColor: results.label_style.borderColor,
                        borderWitch: results.label_style.borderWitch,
                        fontColor: results.label_style.fontColor,
                        fontSize: results.label_style.fontSize
                    }

                })
            }else{
                alert(res.msg)
            }
        })
    }
    //
    changeTag(){
        var list=ConfigData2.this.state;
        if(!list.mapName){
            //alert("");
            $(".PageShow").show().find('h1').html("!");
            global.Time();
        }else if(JSON.stringify(list.labelPosition) === "{}"){
            //alert("");
            $(".PageShow").show().find('h1').html("!");
            global.Time();
        }else{
            Common.onCoordinate();
            setTimeout(function () {
                var list=ConfigData2.this.state;
                var data={
                    id:list.labelId,
                    label_name: list.mapName,
                    center_position: list.centerPosition,
                    label_position: list.labelPosition,
                    label_style: {
                        fontSize:list.fontSize,
                        fontColor:list.fontColor,
                        borderWitch:list.borderWitch,
                        borderColor:list.borderColor
                    },
                    remark: "1"
                }
                console.log(data,list.centerPosition);
                if(JSON.stringify(list.centerPosition) === "{}"){
                    alert("Cesium");
                }else{
                    axios.post(global.Url+'map/label/update',data).then((res)=>{
                        constresults=res.data.data;
                        if(results &&res.data.msg==='success'){
                            console.log(res,results);
                            //alert("");
                            $(".PageShow").show().find('h1').html("!");
                            global.Time();
                            ConfigData2.this.onLoad();
                        }else{
                            alert(res.data.msg)
                        }
                    })
                }


            },10)


        }
    }
    //
    onContextMenu(e,id){
        e.stopPropagation(); //
        $("#Right"+id).find('.RightDelete').css({'display':'block'});
        $("#Right"+id).siblings().find('.RightDelete').css({'display':'none'});
        $("#Right"+id).css({'color':'#54b2df','fontWeight':'bold'});
        $("#Right"+id).siblings().css({'color':'#fff','fontWeight':'100'});
    }
    //
    onChangeData(){
        setTimeout(function(){
            var that = ConfigData2.this.state;
            vardata={
                id:that.labelId,
                labeltxt:that.mapName,
                label_style:{
                    fontSize:that.fontSize,
                    fontColor:that.fontColor,
                    borderWitch:that.borderWitch,//
                    borderColor:that.borderColor
                }

            }
            Common.ModifyLabel(data)
        },10);
    }
    //
    DeletedTag(e,id){
        e.stopPropagation();
        $(".data2Show").show();
        this.setState({
            deletedId:id
        })
    }
    PageDeletedData2(){
        $(".data2Show").hide();
        axios.post(global.Url+'map/label/delete',{id:this.state.deletedId}).then((res) => {
            const result = res.data.data;
            if(result) {
                console.log(result);
                this.onLoad();
                $("#Right"+this.state.deletedId).find('.RightDelete').css({'display':'none'});
                Common.ResetLabel();
                //alert("");
                $(".PageShow").show().find('h1').html("!");
                global.Time();
                ConfigData2.this.AddList();
            }else{
                alert(res.data.msg)
            }
        })
    }
    render(){
        const {isLoad,mapList,oneMap,twoMap,mapName,fontSize,fontColor,borderWitch,borderColor,mapTagName} = this.state;
        return(
            <Fragment>
                {isLoad
                    ?
                    <div className="configShow Map" style={{'width':'460px','height': '400px'}}>
                        <div className="configShowNav"> <i onClick={()=>this.Close()}></i></div>
                        <div className="configData">
                            <div className="configDataLeft">
                                <h1><button onClick={()=>this.AddList()}></button></h1>
                                <ul>
                                    {
                                        mapList.length > 0 && mapList.map(
                                            (item,index) => {
                                                return <li key={index} className='RightDeleted' id={'Right'+item.id} onClick={()=>this.ChangeList(item.id)} onContextMenu={(e)=>this.onContextMenu(e,item.id)}>
                                                    {item.label_name}
                                                    <div className="RightDelete" style={{'display':'none'}}>
                                                        <button onClick={(e)=>this.DeletedTag(e,item.id)}></button>
                                                    </div>
                                                </li>
                                            }
                                        )
                                    }
                                </ul>
                            </div>
                            {oneMap
                                ?
                                <div className="configDataRight">
                                    <h1></h1>
                                    <li><span></span><input type="text" defaultValue={mapName} onChange={(event) => this.changeName(event)}/></li>
                                    <li><span></span><input type="text" defaultValue={fontSize} onChange={(event) => this.changFont(event)}/></li>
                                    <li><span></span>
                                        <select
                                            className='AddSelect'
                                            onChange={this.ChangeColor}
                                            defaultValue = {fontColor}>
                                            <option value='#ff0000'></option>
                                            <option value='#ffd700'></option>
                                            <option value='#436eee'></option>
                                            <option value='#228b22'></option>
                                            <option value='#ffffff'></option>
                                        </select>
                                    </li>
                                    <li><span></span><input type="text" defaultValue={borderWitch} onChange={(event) => this.changeBorder(event)}/></li>
                                    <li><span></span>
                                        <select
                                            className='AddSelect'
                                            onChange={this.ChangeBorderColor}
                                            value = {borderColor}>
                                            <option value='#ffffff'></option>
                                            <option value='#ff0000'></option>
                                            <option value='#ffd700'></option>
                                            <option value='#436eee'></option>
                                            <option value='#228b22'></option>
                                        </select>
                                    </li>
                                    <div className="configBut" ><button onClick={()=>this.saveTag()}></button><button onClick={()=>this.setTag()}></button></div>
                                </div>
                                :''
                            }
                            {twoMap
                                ?
                                <div className="configDataRight">
                                    <h1>{mapTagName}</h1>
                                    <li><span></span><input type="text" value={mapName} onChange={(event) => this.changeName(event)}/></li>
                                    <li><span></span><input type="text" value={fontSize} onChange={(event) => this.changFont(event)}/></li>
                                    <li><span></span>
                                        <select
                                            className='AddSelect'
                                            onChange={this.ChangeColor}
                                            value={fontColor}>
                                            <option value='#ff0000'></option>
                                            <option value='#ffd700'></option>
                                            <option value='#436eee'></option>
                                            <option value='#228b22'></option>
                                            <option value='#ffffff'></option>
                                        </select>
                                    </li>
                                    <li><span></span><input type="text" value={borderWitch} onChange={(event) => this.changeBorder(event)}/></li>
                                    <li><span></span>
                                        <select
                                            className='AddSelect'
                                            onChange={this.ChangeBorderColor}
                                            value = {borderColor}>
                                            <option value='#ffffff'></option>
                                            <option value='#ff0000'></option>
                                            <option value='#ffd700'></option>
                                            <option value='#436eee'></option>
                                            <option value='#228b22'></option>
                                        </select>
                                    </li>
                                    <div className="configBut" ><button onClick={()=>this.changeTag()}></button><button onClick={()=>this.setTag()}></button></div>
                                </div>
                                :''
                            }

                        </div>
                    </div>
                    :''
                }
                <div className="PageShow1 data2Show">
                    <div className="PageShowCont">
                        <h1></h1>
                        <h2><button onClick={()=>this.PageDeletedData2()}></button><button onClick={()=>global.PageHide()}></button></h2>
                    </div>
                </div>
            </Fragment>
        )
    }
}
const mapState = (state) => {
    return{
        adminItem:state.admin.get('adminItem'),
        adminIndex:state.admin.get('adminIndex'),
    }
};
const mapDispatch = (dispatch) => ({
    navClick(id,item) {
        dispatch(actionCreators.nav_click(id,item));
    },
});

export default connect(mapState,mapDispatch)(ConfigData2);