import React,{Fragment,Component} from "react";
import "../../style/common.css";
import moment from "moment";
import axios from "axios";
import {Common} from '../Cesium/method'
import {Link} from "react-router-dom";
import ResourceMap from "./homePage/ResourceMap";
import Surveillance from "./homePage/Surveillance";

class Header extends Component{
    constructor(props){
        super(props);
        this.state=({
            success:false,//falsetrue
            homeTitle:'',//sessionStorage.getItem('userTitle')
            homeUser:'',//
            NavShow:true, //truefalse
            NavNumber:0//id
        });
        var that=this;
        setInterval(function(){
            that.fn()
        },1000)
    }
    componentDidMount() {
        this.fn();
        this.HQuser(); //
        this.HQstate();//
        this.HQConfig();//
        this.onLoadStand();//
        this.RemoveBombox();//
    }
    //
    RemoveBombox(){
        let box=global.WebList;
        for(let l=0; l<box.length; l++){
            Common.removeBubbleBombox(box[l].event_info.id);
        }
    }
    NavChange(id){
        Common.removeBubbleLabel();
        this.RemoveBombox();//
        this.setState({
            NavNumber:id
        })
        global.AlarmShow=false;
        switch (id) {
            case 0:
                //this.RemoveBombox();//
                Common.onMap();
                break;
            case 1:
                //this.RemoveBombox();//
                Common.onResource();
                break;
            case 2:
                //this.RemoveBombox();//
                let list=global.WebList;
                for (let n = 0; n < list.length; n++) {
                    console.log(list);
                    for (let p =n+1; p <list.length; ) {
                        if (list[n].event_info.id === list[p].event_info.id) {
                            list.splice(p, 1);
                        }
                        else p++;
                    }
                }
                global.AlarmShow=true;
                if(global.AlarmShow){
                    for(let l=0; l<list.length; l++){
                        Common.AlarmBox(list[l]);
                    }
                }
                Common.onSurveillance();
                break;
            case 3:
                Common.onMap();
                //this.RemoveBombox();//
                break;
            case 4:
                Common.onMap();
                //this.RemoveBombox();//
                break;
            case 5:
                Common.onMap();
                //this.RemoveBombox();//
                break;
            default:
                return null;
        }
    }
    fn(){
        //json
        var D=moment().format("YYYY.MM.DD HH:mm:ss");
        this.setState({Time:D})
    }
    //
    HQuser(){
        axios.get(global.Url+'sys/user/defaultUser').then((res) => {
            const result = res.data.data;
            if(result) {
                sessionStorage.setItem('userName',result.full_name); //admin
                sessionStorage.setItem('role',result.role_id); //ID
                this.setState({
                    homeUser:result.full_name,
                    success:true  //
                })

            }else{
                alert(res.msg)
            }
        })
    }
    //
    HQstate(){
        axios.get(global.Url+'sys/config/isConfig').then((res) => {
            const result = res.data.data;
            if(result) {
            }else{
                alert(res.msg)
            }
        })
    };
    //
    HQConfig(){
        axios.get(global.Url+'sys/config').then((res) => {
            const result = res.data.data;
            if(result) {
                sessionStorage.setItem('userTitle',result.sys_name);
                this.setState({
                    homeTitle:result.sys_name,
                    success:true
                });
                sessionStorage.setItem('Serverurl',result.data_server_uri); //
                sessionStorage.setItem('data_type',result.data_type);// 
                //console.log(1);
            }else{
                alert(res.msg)
            }
        })
    };
    //
    onLoadStand(){
        axios.get(global.Url+'sys/config/isStand').then((res) => {
            const result = res.data.data;
            if(result) {
                //console.log(result.is_stand,this.state.NavShow);
                this.setState({
                    NavShow:result.is_stand
                })
            }else{
                alert(res.msg)
            }
        })
    }
    render(){
        const {homeTitle,homeUser,Time,success,NavShow,NavNumber} = this.state;
        return(
            <Fragment>
                {
                    NavShow
                        ? <div className="HeaderLeft">{success?homeTitle:''}<i></i></div>
                        : ""
                }
                {
                    NavShow
                    ?
                        <div className="HeaderContentHome">
                            <li className={NavNumber === 0 ? "cur" : ''} onClick={() => this.NavChange(0)}></li>
                            <li className={NavNumber === 1 ? "cur" : ''} onClick={() => this.NavChange(1)}></li>
                            <li className={NavNumber === 2 ? "cur" : ''} onClick={() => this.NavChange(2)}></li>
                            <li className={NavNumber === 3 ? "cur" : ''} onClick={() => this.NavChange(3)}></li>
                            <li className={NavNumber === 4 ? "cur" : ''} onClick={() => this.NavChange(4)}></li>
                            <li className={NavNumber === 5 ? "cur" : ''} onClick={() => this.NavChange(5)}></li>
                        </div>
                    :
                        <div className="HeaderContentHomeNav">
                            <li className={NavNumber === 0 ? "cur" : ''} onClick={() => this.NavChange(0)}></li>
                            <li className={NavNumber === 1 ? "cur" : ''} onClick={() => this.NavChange(1)}></li>
                            <li className={NavNumber === 2 ? "cur" : ''} onClick={() => this.NavChange(2)}></li>
                            <li className={NavNumber === 3 ? "cur" : ''} onClick={() => this.NavChange(3)}></li>
                            <li className={NavNumber === 4 ? "cur" : ''} onClick={() => this.NavChange(4)}></li>
                            <li className={NavNumber === 5 ? "cur" : ''} onClick={() => this.NavChange(5)}></li>
                        </div>
                }
                {
                    NavShow
                        ? <div className="HeaderRightHome">
                            <em></em><span className="HeaderTime">{Time}</span>
                            <Link to='/admin'><span className="HeaderBank" title=''></span></Link>
                            <span className="HeaderUser"><i>{success?homeUser:''}</i></span>
                        </div>
                        : ""
                }
                {(() => {
                    switch (NavNumber) {
                        case 1:
                            return <ResourceMap></ResourceMap>;
                        case 2:
                            return <Surveillance></Surveillance>;
                        default:
                            return null;
                    }
                })()}
            </Fragment>
        )
    }

};

export default Header;