                labelId:'',
                ChangeSave:false,
                label_style:{  //
                    isLining:false,//
                    labelName:'',//
                    fontColor:'#ff0000',//
                    fontSize:'1',//
                    borderColor:'#ffffff',//
                    borderWitch:'1',//
                },
                Polyline_style:{  //
                    PolylineWitch:'1',//
                    PolylineColor:'#ff0000',
                    borderWitchPolyline:'1',//
                    borderColor:'#ffffff',//
                    borderTransparencyPolyline:'50'
                },
                Polygon_style:{   //
                    shapeColor:'#ff0000',//
                    shapeTransparency:'50',//
                    borderWitchPolygon:'1',//
                    borderColor:'#ffffff',//
                    borderTransparency:'50'//
                },
