    //
    onContextMenu(e,id){
        e.stopPropagation();
        $("#layer"+ id).find('span').css({'color':'#54b2df'});
        $("#layer"+ id).siblings().find('span').css({'color':'#fff'});
        $(".layer_t").find('span').css({'color':'#fff'});
        $(".layer").find(".Alert_tk").css('display',"none");
        $("#layer"+id).find('#Alert'+id).css('display',"block");//
    }
    //
    onContextMenu1(e,id){
        e.stopPropagation();
        $(".layer").find("span").css({'color':'#fff'});
        $("#layer_t"+ id).find('span').css({'color':'#54b2df'});
        $("#layer_t"+ id).siblings().find('span').css({'color':'#fff'});
        $(".layer").find(".Alert_tk").css('display',"none");
        $("#layer_t"+id).find('#Alert'+id).css('display',"block");//
    }
    //
    saveTwoTag(){
        const {twoName,modeASelect,options,twoPic} =this.state;
        if(!twoName){
            //alert("")
            $(".PageShow").show().find('h1').html("!");
            global.Time();
        }else if(JSON.stringify(options) === "{}"){
            //alert("");
            $(".PageShow").show().find('h1').html("!");
            global.Time();
        }else{
            switch (modeASelect) {
                case 'label':
                    ADD();
                    break;
                case 'billboard':
                    if(twoPic === AddPic){
                        //alert("")
                        $(".PageShow").show().find('h1').html("!");
                        global.Time();
                    }else{
                        ADD();
                    }
                    break;
                case 'polyline':
                    ADD();
                    break;
                case 'polygon':
                    ADD();
                    break;
                default:
                    break;
            }
            function ADD(){
                axios.post(global.Url+'map/layer/add',{
                    layer_name:twoName,
                    layer_type:modeASelect,
                    group_id:sessionStorage.getItem('numId'),
                    options:options,
                    remark:''
                }).then((res) => {
                    const result = res.data.data;
                    console.log(res,result);
                    if(result) {
                        //alert("");
                        $(".PageShow").show().find('h1').html("!");
                        global.Time();
                        ConfigData3.this.onLayer();
                        ConfigData3.this.setState({
                            twoName:'',//
                            modeASelect:'label',//
                            labelId:'',
                            ChangeSave:false,
                            label_style:{  //
                                isLining:false,//
                                labelName:'',//
                                fontColor:'#ff0000',//
                                fontSize:'1',//
                                borderColor:'#ffffff',//
                                borderWitch:'1',//
                            },
                            Polyline_style:{  //
                                PolylineWitch:'1',//
                                PolylineColor:'#ff0000',
                                borderWitchPolyline:'1',//
                                borderColor:'#ffffff',//
                                borderTransparencyPolyline:'50'
                            },
                            Polygon_style:{   //
                                shapeColor:'#ff0000',//
                                shapeTransparency:'50',//
                                borderWitchPolygon:'1',//
                                borderColor:'#ffffff',//
                                borderTransparency:'50'//
                            },
                            twoPic:AddPic,
                        });
                    }else{
                        alert(res.data.msg)
                    }
                })
            }
        }
    }
    //
    changeSaveTwoTag(){
        Common.ClearHandler(); //
        const {twoName,modeASelect,options,twoPic,labelId} =this.state;
        //console.log(labelId,options);
        if(!twoName){
            //alert("")
            $(".PageShow").show().find('h1').html("!");
            global.Time();
        }else if(JSON.stringify(options) === "{}"){
            //alert("");
            $(".PageShow").show().find('h1').html("!");
            global.Time();
        }else{
            switch (modeASelect) {
                case 'label':
                    ConfigData3.this.setState({
                        options:ConfigData3.this.state.label_style
                    });
                    ADD();
                    break;
                case 'billboard':
                    if(twoPic === AddPic){
                        //alert("")
                        $(".PageShow").show().find('h1').html("!");
                        global.Time();
                    }else{
                        ADD();
                    }
                    break;
                case 'polyline':
                    ConfigData3.this.setState({
                        options:ConfigData3.this.state.Polyline_style
                    });
                    ADD();
                    break;
                case 'polygon':
                    ConfigData3.this.setState({
                        options:ConfigData3.this.state.Polygon_style
                    });
                    ADD();
                    break;
                default:
                    break;
            }
            function ADD(){
                var list ={
                    layer_name:twoName,
                    layer_type:modeASelect,
                    group_id:sessionStorage.getItem('numId'),
                    id:labelId,
                    options:options.label_name = ConfigData3.this.state.label_style.labelName
                };
                console.log(list);
                axios.post(global.Url+'map/layer/update',{
                    id:labelId,
                    layer_name:twoName,
                    layer_type:modeASelect,
                    group_id:sessionStorage.getItem('numId'),
                    options:options,
                    remark:''
                }).then((res) => {
                    const result = res.data.data;
                    console.log(res,result);
                    if(result) {
                        //alert("");
                        $(".PageShow").show().find('h1').html("!");
                        global.Time();
                        ConfigData3.this.onLayer();
                    }else{
                        alert(res.data.msg)
                    }
                })
            }
        }
    }
    //
    changeName(e){
        this.setState({
            groupName:e.target.value
        })
    }
    //
    changeTwoName(e){
        this.setState({
            twoName:e.target.value
        })
    }
    //
    changeLabelName(e){
        ConfigData3.this.state.label_style.labelName = e.target.value;
        this.onChangeData();
    }
    //
    handleIsLining(e) {
        var a= ConfigData3.this.state.label_style;
        a.isLining = !ConfigData3.this.state.label_style.isLining;
        ConfigData3.this.setState(prevState => (a));
        // console.log( ConfigData3.this.state.label_style.isLining);
        this.onChangeData();
    }
    //
    ChangeColor(e){
        ConfigData3.this.state.label_style.fontColor = e.target.value;
        this.onChangeData();
    }
    //
