import React,{Fragment,PureComponent} from "react";
import {connect} from 'react-redux';
import {actionCreators} from "../../store";
import axios from "axios";
import $ from "jquery";
import {Common} from "../../../Cesium/method";

class ConfigData4 extends PureComponent{
    constructor(props) {
        super(props);
        this.state={
            isLoad:true,
            treeList:{},//
            regionName:'',//
            oneInfo:true,//
            Pid:0,//pid,0id
            Id:'',//id
            addChange:false,
            twoInfo:false,//
            twoImport:false,//
            SaveId:'',//id
            isIndoor:false,//
            build:{},//
            buildId:'',//ID
            floor:{},//
            floorId:'',//ID
            SHP:'',//.shp
            DBF:'',//.dbf
            gridName:'',//
            labelId:'',//id
            TwoItem:true,//
            TwoItemTwo:false,//
            gridStyle:{//
                color:'#436eee',//
                colorTransparency:50,//
                borderWidth:'1',//
                borderColor:'#ff0000',//
                borderTransparency:50,//
            },//
            tableList:{},//
            deleteName:"",//
            deleteId:'',//Id
        }
        ConfigData4.this = this;
        this.Close = this.Close.bind(this);//
        this.onContextMenu = this.onContextMenu.bind(this);//
        this.addInfo = this.addInfo.bind(this);//12
        this.saveInfo = this.saveInfo.bind(this);//
        this.onMenuClicked = this.onMenuClicked.bind(this);//
        this.deleteInfo = this.deleteInfo.bind(this);//
        this.importInfo = this.importInfo.bind(this);//
        this.saveTwoTag = this.saveTwoTag.bind(this);//
        this.navItemClick = this.navItemClick.bind(this);//
        this.saveSure = this.saveSure.bind(this);//
        this.onReset = this.onReset.bind(this);//
        this.PageDeletedData4 = this.PageDeletedData4.bind(this);
    }
    //
    Close(){
        this.setState({
            isLoad:false
        })
    }
    componentDidMount() {
        //
        this.onLoad();
        //
        document.oncontextmenu = function(){
            return false;
        }
    }
    //
    onLoad(){
        var list=[];
        axios.get(global.Url+'grid/region/list').then((res) => {
            const result = res.data.data;
            if(result) {
                list=result;
                console.log(list);
                axios.post(global.Url+'grid/info/list').then((res) => {
                    const results = res.data.data;
                    if(results) {
                        console.log(results);
                        results.forEach(element=>{
                            vararr={
                                id:element.id
                                ,pid:element.region_id
                                ,region_name:element.grid_name
                                ,node_type:"details"
                                ,position:element.position
                                ,indoor:element.indoor
                                ,build_id:element.build_id
                                ,floor_id:element.floor_id
                                ,children:[],
                                grid_style:element.grid_style,
                                geom:element.geom
                            }
                            list.push(arr);
                        });
                        this.setState({
                            treeList:list
                        })
                        console.log(list);

                    }else{
                        alert(res.data.msg)
                    }
                })


            }else{
                alert(res.data.msg)
            }
        })
    }
    //
    AnalyticFormat(vdom) {
        var menuObj = vdom;
        //
        function getTree(data, Pid) {
            let result = [];
            let temp;
            for (let i = 0; i < data.length; i++) {
                if (data[i].pid === Pid) {
                    temp = getTree(data, data[i].id);
                    /*            if (temp.length > 0) {
                                    data[i].children = temp
                                }*/
                    data[i].children = temp
                    result.push(data[i])

                }
            }
            return result


        }

        return getTree(menuObj, "0");

    }
    generateMenu(menuObj) {
        let vdom = [];
        if (menuObj instanceof Array) {
            let list = [];
            for (var item of menuObj) {
                list.push(this.generateMenu(item));
            }
            vdom.push(
                <ul key="single">
                    {list}
                </ul>
            );
        } else {
            //console.log(menuObj);
            if(menuObj == null){return;}
            vdom.push(
                <li key={menuObj.id} className='layer' id={'layer'+menuObj.id} onContextMenu={menuObj.node_type === 'details'?(e)=>this.onContextMenu1(e):(e)=>this.onContextMenu(e,menuObj.id)}>
                    <span  onClick={menuObj.node_type === 'details'?(e)=>this.onMenuClicked(e,menuObj,1):(e)=>this.onMenuClicked(e,menuObj,2)} >
                        {(() => {
                            switch (menuObj.node_type) {
                                case 'group':
                                    return <i className='group'></i>;
                                case 'grid':
                                    return <i className='grid'></i>;
                                case 'details':
                                    return <i className='details'></i>;
                                default:
                                    return null;
                            }
                        })()}
                        {menuObj.region_name}
                    </span>
                    {this.generateMenu(menuObj.children)}
                    <div className="Alert_tk" id={'Alert'+menuObj.id} style={{'display':'none'}}>
                        {(() => {
                            switch (menuObj.node_type) {
                                case 'group':
                                    return <div>
                                        <button onClick={(e)=>this.addInfo(e,2,menuObj.id)}></button>
                                        <button onClick={(e)=>this.addInfo(e,3,menuObj)}></button>
                                        {menuObj.children.length<1?<button onClick={(e) =>this.deleteInfo(e,menuObj.id)}></button>:''}
                                        <button onClick={(e)=>this.importInfo(e,menuObj)}></button>
                                    </div>;
                                case 'grid':
                                    return <div>
                                        <button onClick={(e)=>this.addInfo(e,3,menuObj)}></button>
                                        <button onClick={(e)=>this.onReset(e,menuObj.id)}></button>
                                    </div>;
                                default:
                                    return null;
                            }
                        })()}
                    </div>
                </li>
            );
        }
        return vdom;
    }
    //
    onMenuClicked(ev,name,ID) {
        console.log(name);
        ev.stopPropagation();
        // <h1>
        let node = $(ev.target);

        // <h1>
        let subMenu = node.next();

        // /
        subMenu.css("display", subMenu.css('display') === "none" ? "block" : "none");
        $("#layer"+ name.id).parents().find('span').css({'color':'#fff','fontWeight':'100'});
        $("#layer"+ name.id).find('span').css({'color':'#54b2df','fontWeight':'bold'});
        $("#layer"+ name.id).siblings().find('span').css({'color':'#fff','fontWeight':'100'});
        $("#layer"+ name.id).children().find('span').css({'color':'#fff','fontWeight':'100'});
        $(".layer_t").find('span').css({'color':'#fff','fontWeight':'100'});
        $(".layer").find(".Alert_tk").css('display',"none");
        letlist=this.state.treeList;
        list.forEach(element=>{
            if(element.pid===name.pid){
                Common.Emptyentities({id:element.id})
            }
        });
        if(ID===1){
            letdata={
                style:name.grid_style,
                gridList:name.geom.coordinates,
                flyTo:true,
                id:name.id
            }
            Common.showGrid(data);
            ConfigData4.this.setState({
                twoInfo:true,
                oneInfo:false,
                twoImport:false,
                gridName:name.region_name,
                gridStyle:name.grid_style,
                labelId:name.id,
                TwoItem:true,
                TwoItemTwo:false
            })
            $(".TwoDataNav").find('span:last-child').removeClass('cur');
            $(".TwoDataNav").find('span:first-child').addClass('cur');
        }else if(ID===2){
            return
        }

    }
    //
    onContextMenu(e,id){
        e.stopPropagation();
        console.log(id)
        $("#layer"+ id).parents().find('span').css({'color':'#fff','fontWeight':'100'});
        $("#layer"+ id).find('span').css({'color':'#54b2df','fontWeight':'bold'});
        $("#layer"+ id).siblings().find('span').css({'color':'#fff','fontWeight':'100'});
        $("#layer"+ id).children().find('span').css({'color':'#fff','fontWeight':'100'});
        $(".layer_t").find('span').css({'color':'#fff','fontWeight':'100'});
        $(".layer").find(".Alert_tk").css('display',"none");
        $("#layer"+id).find('#Alert'+id).css('display',"block");//
    }
    onContextMenu1(e){
        e.stopPropagation();
        $(".layer").find(".Alert_tk").css('display',"none");
    }
    //
    addInfo(e,num,Item){ //123
        $(".layer").find(".Alert_tk").css('display',"none");
        e.stopPropagation();
        this.setState({
            Pid:Item,
            oneInfo:true,
            regionName:'',
            addChange:false,
            twoInfo:false,
            twoImport:false,
        })
        if(num === 1){
            console.log('')
        }else if(num === 2){
            console.log('')
        }else if(num === 3){
            this.setState({
                Pid:Item.pid,
                regionName:Item.region_name,
                addChange:true,
                Id:Item.id,
            })
        }
    }
    //
    saveInfo(num){  //num12
        if(num === 1 ){
            debugger
            const {regionName} = this.state;
            if(!regionName){
                $(".PageShow").show().find('h1').html("!");
                global.Time();
            }else{
                axios.post(global.Url+'grid/region/add',{
                    region_name:ConfigData4.this.state.regionName,
                    pid:ConfigData4.this.state.Pid
                }).then((res) => {
                    const result = res.data.data;
                    if(result) {
                        console.log(result);
                        //alert("");
                        $(".PageShow").show().find('h1').html("!");
                        global.Time();
                        this.onLoad();
                        this.setState({
                            regionName:'',
                            Pid:0,
                        })
                    }else{
                        alert(res.data.msg)
                    }
                })
            }

        }else if(num===2){
            axios.post(global.Url+'grid/region/update',{
                region_name:ConfigData4.this.state.regionName,
                id:ConfigData4.this.state.Id,
                pid:ConfigData4.this.state.Pid
            }).then((res) => {
                const result = res.data.data;
                if(result) {
                    console.log(result);
                    //alert("");
                    $(".PageShow").show().find('h1').html("!");
                    global.Time();
                    this.setState({
                        addChange:false,
                    })
                    this.onLoad();
                    this.setState({
                        regionName:'',
                        Pid:0,
                    })
                }else{
                    alert(res.data.msg)
                }
            })
        }

    }
    //
    deleteInfo(e,item){
        e.stopPropagation();
        ConfigData4.this.setState({
            deleteName:"",
            deleteId:item
        })
        $(".data4Show").show().find('h1').html("");
    }
    //
    onReset(e,item){
        e.stopPropagation();
        ConfigData4.this.setState({
            deleteName:"",
            deleteId:item
        })
        $(".data4Show").show().find('h1').html("");
    }
    PageDeletedData4(){
        $(".data4Show").hide();
        const {deleteName,deleteId} =this.state;
        if(deleteName===""){
            axios.post(global.Url+'grid/region/delete',{id:deleteId}).then((res) => {
                const result = res.data.data;
                if(result) {
                    console.log(result);
                    //alert("");
                    $(".PageShow").show().find('h1').html("!");
                    global.Time();
                    this.onLoad();
                }else{
                    alert(res.data.msg)
                }
            })
        }else{
            axios.post(global.Url+'grid/region/reset',{id:deleteId}).then((res) => {
                const result = res.data.data;
                if(result) {
                    console.log(result);
                    //alert("");
                    $(".PageShow").show().find('h1').html("!");
                    global.Time();
                    this.onLoad();
                }else{
                    alert(res.data.msg)
                }
            })
        }
    }
    //
    importInfo(e,item){
        $(".layer").find(".Alert_tk").css('display',"none");
        e.stopPropagation();
        console.log(item);
        this.setState({
            oneInfo:false,
            twoInfo:false,
            twoImport:true,
            SaveId:item.id
        })
    }
    //
    importFile(event){
        var File=event.target.files;
        varform=newFormData();
        console.log(File[1],File[0])
        form.append("shp",File[1]);
        form.append("dbf",File[0]);
        this.setState({
            SHP:File[1],
            DBF:File[0]
        })
        var settings={
            "url":global.Url+'grid/info/analysisSHP',
            "method":"POST",
            "timeout":0,
            "processData":false,
            "mimeType":"multipart/form-data",
            "contentType":false,
            "data":form,
            "dataType":"JSON"
        };
        console.log(form);
        debugger
        $.ajax(settings).done(function(res){
            console.log(res);
            const result = res.data;
            if(result) {
                var data = {
                    style :ConfigData4.this.state.gridStyle,
                    gridList:result,
                }
                console.log(result,ConfigData4.this.state.gridStyle,data);
                Common.PreviewGrid(data);

            }else{
                alert(res.msg)
            }
        });

    }
    //
    changeRegionName(e){
        this.setState({
            regionName:e.target.value
        })
    }
    //
    handleIsIndoor(e){
        this.setState(prevState => ({
            isIndoor: !prevState.isIndoor
        }));
        if(!this.state.isIndoor){
            //alert("")
            axios.get(global.Url+'map/build/list').then((res) =>{
                const result = res.data.data;
                if(result) {
                    console.log(result);
                    this.setState({
                        build:result,
                        //buildValue:result[0].build_name,
                        buildId:result[0].id,
                    })
                    axios.post(global.Url+'map/floor/list',{build_id:result[0].id}).then((res) =>{
                        const result = res.data.data;
                        if(result) {
                            console.log(result);
                            this.setState({
                                floor:result,
                                //floorValue:result[0].floor_name,
                                floorId:result[0].id
                            })
                        }else{
                            alert(res.data.msg)
                        }
                    })
                }else{
                    alert(res.data.msg)
                }
            })

        }else{
            //alert("")
            this.setState({
                build:{},
                buildValue:'',
                buildId:'',
                floor:{},
                floorValue:'',
                floorId:''
            })
        }
    }
    //
    ChangeBuild = e =>{
        e.preventDefault();
        console.log(e.target.value);
        axios.post(global.Url+'map/floor/list',{build_id:e.target.value}).then((res) =>{
            const result = res.data.data;
            if(result) {
                console.log(result);
                this.setState({
                    floor:result,
                    //floorValue:result[0].floor_name,
                    floorId:result[0].id
                })
            }else{
                alert(res.data.msg)
            }
        })
        this.setState({
            buildId:e.target.value
        })
    }
    //
    ChangeFloor = e =>{
        e.preventDefault();
        console.log(e.target.value);
        this.setState({
            floorId:e.target.value
        })
    }
    //
    onChangeData(){
        setTimeout(function(){
            var that = ConfigData4.this.state;
            vardata={
                ids:[that.labelId],
                style:that.gridStyle,
            }
            Common.modifyGrid(data)
        },10);
    }
    //
    ChangeColor(e){
        //ConfigData4.this.state.gridStyle.color = e.target.value;
        var tarns1 = ConfigData4.this.state.gridStyle;
        tarns1.color = e.target.value;
        ConfigData4.this.setState(tarns1);
        this.onChangeData();
    }
    //
    changeColorTransparency(e){
        var tarns1 = ConfigData4.this.state.gridStyle;
        tarns1.colorTransparency = e.target.value;
        ConfigData4.this.setState(tarns1);
        this.onChangeData();
    }
    //
    ChangeBorderWidth(e){
        //ConfigData4.this.state.gridStyle.borderWidth = e.target.value;
        var tarns1 = ConfigData4.this.state.gridStyle;
        tarns1.borderWidth = e.target.value;
        ConfigData4.this.setState(tarns1);
        this.onChangeData();
    }
    //
    changeBorderColor(e){
        //ConfigData4.this.state.gridStyle.borderColor = e.target.value;
        var tarns1 = ConfigData4.this.state.gridStyle;
        tarns1.borderColor = e.target.value;
        ConfigData4.this.setState(tarns1);
        this.onChangeData();
    }
    //
    changeBorderTransparency(e){
        var tarns2 = ConfigData4.this.state.gridStyle;
        tarns2.borderTransparency = e.target.value;
        ConfigData4.this.setState(tarns2);
        this.onChangeData();
    }
    //
    GridName(e){
        ConfigData4.this.setState({
            gridName:e.target.value
        });
    }
    //
    saveTwoTag(){
        const {SaveId,SHP,DBF,isIndoor,buildId,floorId,gridStyle} =this.state;
        console.log(SaveId,SHP,DBF,isIndoor,buildId,floorId,gridStyle,gridStyle.color);
        varform=newFormData();
        form.append("shp",SHP);
        form.append("dbf",DBF);
        form.append("region_id",SaveId);
        form.append("indoor",isIndoor);
        form.append("build_id",buildId);
        form.append("floor_id",floorId);
        form.append("grid_style",JSON.stringify(gridStyle));
        console.log(form);
        if(!SHP){
            //alert("")
            $(".PageShow").show().find('h1').html("!");
            global.Time();
        }else{
            var settings={
                "url":global.Url+'grid/info/upload',
                "method":"POST",
                "timeout":0,
                "processData":false,
                "mimeType":"multipart/form-data",
                "contentType":false,
                "data":form,
                "dataType":"JSON"

            };
            $.ajax(settings).done(function(res){
                console.log(res);
                const result = res.data;
                if(result) {
                   console.log(result);
                   alert("!");
                    axios.post(global.Url+'grid/info/list').then((res) => {
                        const result = res.data.data;
                        if(result) {
                            console.log(result);
                            ConfigData4.this.onLoad();
                            ConfigData4.this.setState({
                                regionName:'',
                                Pid:0,
                                oneInfo:true,
                                twoInfo:false,
                                twoImport:false
                            })

                        }else{
                            alert(res.data.msg)
                        }
                    })

                }else{
                    alert(res.msg)
                }
            });
        }

    }
    //
    navItemClick(item){
        if(item === 1){
            ConfigData4.this.setState({
                TwoItem:true,
                TwoItemTwo:false
            })
            $(".TwoDataNav").find('span:last-child').removeClass('cur');
            $(".TwoDataNav").find('span:first-child').addClass('cur');
        }else if(item === 2){
            ConfigData4.this.setState({
                TwoItem:false,
                TwoItemTwo:true
            })
            $(".TwoDataNav").find('span:last-child').addClass('cur');
            $(".TwoDataNav").find('span:first-child').removeClass('cur');
            let tableList=[];
            axios.post(global.Url+'device/category/categoryByGrid',{id:ConfigData4.this.state.labelId}).then((res) => {
                const result = res.data.data;
                if(result) {
                    console.log(result);
                    tableList=result;
                    axios.post(global.Url+'device/info/deviceByGrid',{id:ConfigData4.this.state.labelId}).then((res) => {
                        const results = res.data.data;
                        if(results) {
                            tableList.forEach(elements=>{
                                elements.subsets = [];
                            });
                            results.forEach(element=>{
                                tableList.forEach(elements=>{
                                    if(element.category_name === elements.category_name){
                                        elements.subsets.push(element)
                                    }
                                });
                            });
                            console.log(tableList);
                            ConfigData4.this.setState({
                                tableList:tableList
                            })
                        }else{
                            alert(res.data.msg)
                        }
                    })
                }else{
                    alert(res.data.msg)
                }
            })
        }
    }
    //
    saveSure(){
        const {gridName,labelId,gridStyle} = this.state;
        axios.post(global.Url+'grid/info/update',{
            id:labelId,
            grid_name: gridName,
            grid_style: gridStyle
        }).then((res) => {
            const result = res.data.data;
            if(result) {
                console.log(result);
                ConfigData4.this.onLoad();
                //alert("")
                $(".PageShow").show().find('h1').html("!");
                global.Time();
            }else{
                alert(res.data.msg)
            }
        })
    }
    render(){
        const {isLoad,treeList,regionName,oneInfo,addChange,twoInfo,twoImport,isIndoor,buildId,build,floorId,floor,gridStyle,gridName,TwoItem,TwoItemTwo,tableList} =this.state;
        return(
            <Fragment>
                {isLoad?
                    <div className="configShow" style={{'width':'530px'}}>
                        <div className="configShowNav"><i onClick={()=>this.Close()}></i></div>
                        <div className="configData" style={{'height':'400px'}}>
                            <div className="LayerLeftNew">
                                <div className="LeftList">
                                    <div className="LeftListTitle">
                                        <button onClick={(e)=>this.addInfo(e,1,0)}></button>
                                    </div>
                                    <div className="TreeList">
                                        <ul>
                                            {this.generateMenu(this.AnalyticFormat(treeList))}
                                        </ul>
                                    </div>
                                </div>
                            </div>
                            <div className="configDataRight LayerRight" style={{'width':'279px'}}>
                                {oneInfo?
                                    <div>
                                        <li><span></span><input type="text" value={regionName} onChange={(event) => this.changeRegionName(event)}/></li>

                                        <div className="configBut" >{addChange?<button onClick={()=>this.saveInfo(2)}></button>:<button onClick={()=>this.saveInfo(1)}></button>}</div>
                                    </div>
                                    :''}
                                {twoImport?
                                    <div>
                                        <div className="LeftListTitle" style={{'marginLeft':'15px'}}>
                                             <input type="file" name="filename" multiple="multiple" onChange={(e)=> this.importFile(e)}/>
                                            <button></button>
                                        </div>
                                        <div className='AddImport'>
                                            <div className="PositionFloor">
                                                <span><label></label><input type="checkbox" checked={isIndoor} onChange={(e) => this.handleIsIndoor(e)}/></span>
                                                <span>
                                                    <select defaultValue={buildId} onChange={this.ChangeBuild}>
                                                          {
                                                              build.length > 0 && build.map((ele,index)=>{
                                                                  return(
                                                                      <option key={index} value={ele.id}>{ele.build_name}</option>
                                                                  )
                                                              })
                                                          }
                                                    </select>
                                                </span>
                                                <span>
                                                    <select defaultValue={floorId} onChange={this.ChangeFloor}>
                                                          {
                                                              floor.length > 0 && floor.map((ele,index)=>{
                                                                  return(
                                                                      <option key={index} value={ele.id}>{ele.floor_name}</option>
                                                                  )
                                                              })
                                                          }
                                                    </select>
                                                </span>
                                            </div>
                                            <li><span></span></li>
                                            <li><span></span>
                                                <select
                                                    className='AddSelect'
                                                    onChange={(e)=>this.ChangeColor(e)}
                                                    defaultValue = {gridStyle.color}>
                                                    <option value='#ff0000'></option>
                                                    <option value='#ffd700'></option>
                                                    <option value='#436eee'></option>
                                                    <option value='#228b22'></option>
                                                    <option value='#ffffff'></option>
                                                </select>
                                            </li>
                                            <li><span></span>
                                                <input type="range" max='100' min='0' defaultValue={gridStyle.colorTransparency}
                                                       onChange={(event) => this.changeColorTransparency(event)}/>
                                                <i>{gridStyle.colorTransparency}%</i>
                                            </li>
                                            <li><span></span><input type="text" defaultValue={gridStyle.borderWidth} onChange={(event) => this.ChangeBorderWidth(event)}/></li>
                                            <li><span></span>
                                                <select
                                                    className='AddSelect'
                                                    onChange={(e)=>this.changeBorderColor(e)}
                                                    defaultValue = {gridStyle.borderColor}>
                                                    <option value='#ff0000'></option>
                                                    <option value='#ffd700'></option>
                                                    <option value='#436eee'></option>
                                                    <option value='#228b22'></option>
                                                    <option value='#ffffff'></option>
                                                </select>
                                            </li>
                                            <li><span></span>
                                                <input type="range" max='100' min='0' defaultValue={gridStyle.borderTransparency}
                                                       onChange={(event) => this.changeBorderTransparency(event)}/>
                                                <i>{gridStyle.borderTransparency}%</i>
                                            </li>
                                        </div>
                                        <div className="configBut" ><button onClick={()=>this.saveTwoTag()}></button></div>
                                    </div>
                                    :''
                                }
                                {twoInfo?
                                    <div>
                                        <div className="TwoDataNav"><span className='cur' onClick={()=>this.navItemClick(1)}></span><span onClick={()=>this.navItemClick(2)}></span></div>
                                        <div className="one">
                                            {TwoItem?
                                                <div>
                                                    <div className='AddImport one'>
                                                        <li><span></span><input type="text" value={gridName} onChange={(e)=>this.GridName(e)}/></li>
                                                        <li><span></span>
                                                            <select
                                                                className='AddSelect'
                                                                onChange={(e)=>this.ChangeColor(e)}
                                                                value = {gridStyle.color}>
                                                                <option value='#ff0000'></option>
                                                                <option value='#ffd700'></option>
                                                                <option value='#436eee'></option>
                                                                <option value='#228b22'></option>
                                                                <option value='#ffffff'></option>
                                                            </select>
                                                        </li>
                                                        <li><span></span>
                                                            <input type="range" max='100' min='0' value={gridStyle.colorTransparency}
                                                                   onChange={(event) => this.changeColorTransparency(event)}/>
                                                            <i>{gridStyle.colorTransparency}%</i>
                                                        </li>
                                                        <li><span></span><input type="text" value={gridStyle.borderWidth} onChange={(event) => this.ChangeBorderWidth(event)}/></li>
                                                        <li><span></span>
                                                            <select
                                                                className='AddSelect'
                                                                onChange={(e)=>this.changeBorderColor(e)}
                                                                value = {gridStyle.borderColor}>
                                                                <option value='#ff0000'></option>
                                                                <option value='#ffd700'></option>
                                                                <option value='#436eee'></option>
                                                                <option value='#228b22'></option>
                                                                <option value='#ffffff'></option>
                                                            </select>
                                                        </li>
                                                        <li><span></span>
                                                            <input type="range" max='100' min='0' value={gridStyle.borderTransparency}
                                                                   onChange={(event) => this.changeBorderTransparency(event)}/>
                                                            <i>{gridStyle.borderTransparency}%</i>
                                                        </li>
                                                    </div>
                                                    <div className="configBut" ><button onClick={()=>this.saveSure()}></button></div>
                                                </div>
                                                :''
                                            }
                                            {TwoItemTwo?
                                                <div className='AddImport two'>
                                                    <div className="GridNav">
                                                        {
                                                            tableList.length > 0 && tableList.map(
                                                                (item,index) => {
                                                                    return  <li key={index}>{item.category_name}</li>
                                                                }
                                                            )
                                                        }
                                                    </div>
                                                    <div className="GridNavTable">
                                                        <table>
                                                            <thead>
                                                                <tr>
                                                                    <th></th>
                                                                    <th></th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                {
                                                                    tableList.length > 0 && tableList.map(
                                                                        (item,index) => {
                                                                            return (
                                                                                <Fragment key={index}>
                                                                                    {
                                                                                        item.subsets.length > 0 && item.subsets.map(
                                                                                            (name,ele) => {
                                                                                                return  <tr key={ele}><td>{name.device_type}</td><td>{name.device_name}</td></tr>
                                                                                            }
                                                                                        )
                                                                                    }
                                                                                </Fragment>
                                                                            )
                                                                        }
                                                                    )
                                                                }
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                                :''
                                            }

                                        </div>
                                    </div>
                                    :''
                                }
                            </div>

                        </div>
                    </div>
                    :''
                }
                <div className="PageShow1 data4Show">
                    <div className="PageShowCont">
                        <h1></h1>
                        <h2><button onClick={()=>this.PageDeletedData4()}></button><button onClick={()=>global.PageHide()}></button></h2>
                    </div>
                </div>
            </Fragment>
        )
    }
}
const mapState = (state) => {
    return{
        adminItem:state.admin.get('adminItem'),
        adminIndex:state.admin.get('adminIndex'),
    }
};
const mapDispatch = (dispatch) => ({
    navClick(id,item) {
        dispatch(actionCreators.nav_click(id,item));
    },
});

export default connect(mapState,mapDispatch)(ConfigData4);