import React,{Fragment,Component} from 'react';
import "../../style/common.css";
import Header from './indexHome';
import CesiumConent from '../Cesium/index';
import DocumentTitle from "react-document-title";
import axios from "axios";
import Footer from '../footer/index'
import Alarm from "../public/Alarm";

class Index extends Component {
    constructor(props){
        super(props);
        this.state = {
            //isLoading:true,
            isLogin:'',
        }
    }

    componentDidMount(){
        this.onLoad(); //
        if(navigator.userAgent.toLowerCase().indexOf("chrome") === -1){
            alert('');
            window.open('http://rj.baidu.com/soft/detail/14744.html?ald','_blank');
        }else{
        }
    }
    //
    onLoad(){
        axios.get(global.Url+'sys/config/isLogin').then((res) => {
            const result = res.data.data;
            if(result) {
                //console.log(result);
                //
                sessionStorage.setItem('isLogin',result.is_login);
                const is_user=sessionStorage.getItem('user'); //'user'
//                debugger
                if(result.is_login === false){ //falselogin,
                    if(is_user!== "admin"){
                        this.props.history.push( '/login')
                    }else{
                    }
                }else{

                }

            }else{
                alert(res.msg)
            }
        })
    }
    render() {
        return (
            <DocumentTitle title=''>
                <Fragment>
                    <div className="header"><Header></Header></div>
                    <div className="IndexHome">
                        <div className="left"></div>
                        <div className="right"></div>
                        <div className="conent">
                            <CesiumConent></CesiumConent>
                        </div>
                    </div>
                    <Footer></Footer>
                    <Alarm></Alarm>
                </Fragment>
            </DocumentTitle>
        )
    }

}



export default Index;
