    //
    handleIsIndoor(e){
        this.setState(prevState => ({
            indoor: !prevState.indoor
        }));
        if(!this.state.indoor){
            //alert("")
            axios.get(global.Url+'map/build/list').then((res) =>{
                const result = res.data.data;
                if(result) {
                    console.log(result);
                    this.setState({
                        build:result,
                        //buildValue:result[0].build_name,
                        buildId:result[0].id,
                    })
                    axios.post(global.Url+'map/floor/list',{build_id:result[0].id}).then((res) =>{
                        const result = res.data.data;
                        if(result) {
                            console.log(result);
                            this.setState({
                                floor:result,
                                //floorValue:result[0].floor_name,
                                floorId:result[0].id
                            })
                        }else{
                            alert(res.data.msg)
                        }
                    })
                }else{
                    alert(res.data.msg)
                }
            })

        }else{
            //alert("")
            this.setState({
                build:{},
                buildValue:'',
                buildId:'',
                floor:{},
                floorValue:'',
                floorId:''
            })
        }
    }
    //
    ChangeBuild = e =>{
        e.preventDefault();
        console.log(e.target.value);
        axios.post(global.Url+'map/floor/list',{build_id:e.target.value}).then((res) =>{
            const result = res.data.data;
            if(result) {
                console.log(result);
                this.setState({
                    floor:result,
                    floorId:result[0].id
                })
            }else{
                alert(res.data.msg)
            }
        })
        this.setState({
            buildId:e.target.value
        })
    }
    //
    ChangeFloor = e =>{
        e.preventDefault();
        console.log(e.target.value);
        this.setState({
            floorId:e.target.value
        })
    }
